:root {
  --primary-text-color: #111321;
  --primary-bg-color: #f4f4f4;
  --dark-bg-color: #686868;
  --footer-bg-color: #7a7a7a;
  --accent-color: #4440f6;
  --categories-link-color: #4440f7;
  --switcher-color: #4b48db;
  --theme-color: #5f6775;
  --header-underline-color: rgba(0, 0, 0, .2);
  --gray-color: #a8a8a8;
  --black-color: #000;
  --white-color: #fff;
  --light-color: #f8f8f8;
  --article-btn-color: #111321;
  --categories-filter-bnt--bgcolor: #f4f4f4;
  --news-overlay-color: rgba(244, 244, 244, .4);
  --news-already-read-color: #00dd73;
  --news-article-category-bg-color: rgba(68, 64, 247, .7);
}

[dark] {
  --primary-text-color: #f4f4f4;
  --primary-bg-color: #2e2e2e;
  --header-underline-color: rgba(244, 244, 244, .2);
  --categories-filter-bnt-bg-color: #fff;
}

body {
  background-color: var(--primary-bg-color);
  color: var(--primary-text-color);
  font-family: Manrope, sans-serif;
}

.container {
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.dark-theme {
  color: getColor("primary-bg-color");
  background-color: getColor("dark-bg-color");
  fill: getColor("primary-bg-color");
}

html, body {
  height: 100%;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  fill: currentColor;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header-link__modal {
  margin-bottom: 68px;
  display: inline-flex;
}

.header-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  margin-left: auto;
  display: inline-flex;
}

.header-btn__open-modal {
  fill: var(--primary-text-color);
  padding: 3px;
}

.header-btn__close-modal {
  fill: var(--primary-text-color);
  padding: 6px;
  position: absolute;
  top: 24px;
  right: 16px;
}

@media screen and (min-width: 768px) {
  .header-btn {
    display: none;
  }
}

.mobile-modal {
  width: 100%;
  height: 100%;
  z-index: 11;
  pointer-events: none;
  visibility: hidden;
  background-color: var(--primary-bg-color);
  padding: 24px 16px;
  transition: transform .6s cubic-bezier(.4, 0, .2, 1), visibility .6s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  transform: translateY(-100%);
}

.mobile-modal.is-open {
  pointer-events: initial;
  visibility: visible;
  transform: translateY(0);
}

@media screen and (min-width: 768px) {
  .mobile-modal {
    display: none;
  }
}

.modal-list {
  margin: 0;
  padding: 0;
}

.modal-list__item {
  margin: 0 -16px;
  padding: 6px 16px;
  list-style: none;
}

.modal-list__item:not(:last-child) {
  margin-bottom: 18px;
}

.modal-list__item--current {
  color: #f4f4f4;
  background-color: var(--accent-color);
}

.modal-list__link {
  color: var(--primary-text-color);
